import React from 'react';
import './InputBox.styles.scss'

const InputBox = React.forwardRef(( {name, placeholder, onClick, styles, onChange, id, type , onKeyPress, maxlength, minlength, required }, ref ) => {
    return (
        <input required={required} maxlength={maxlength} minlength={minlength} onKeyDown={onKeyPress} id={id} type={type} onChange={onChange} onClick={onClick} ref={ref} name={name} placeholder={placeholder} className={`InputBox ${styles}`} ></input>
    );
});

export default InputBox;