import React from 'react';
import { connect } from 'react-redux'

import './QuestionnaireCardContact.styles.scss';

import InputBox from '../../InputBox/InputBox.component';
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import CTA from '../../CTA/CTA.component';

import { number, email } from '../../../redux/quoteForm/quoteForm.actions';
import { moveForwardsAction } from '../../../redux/questionnaireStage/questionnaireStage.actions';

const QuestionnaireCardContact = ({ setNumber, setEmail, moveF }) => {
    return (
        <section className='QuestionnaireCardContact' >
                <QuestionnaireTitle >How do you want the information sent?</QuestionnaireTitle>
                <InputBox onChange={e => setNumber(e.target.value)} placeholder='Phone #' name='phoneNumber' />
                <InputBox onChange={e => setEmail(e.target.value)} placeholder='Email @' name='email' />
                <CTA onClick={moveF} >Next</CTA>
            
        </section>
    );
};

const mapDispathToProps = dispatch => ({
    setNumber: phoneNumber => dispatch(number(phoneNumber)),
    setEmail: userEmail => dispatch(email(userEmail)),
    moveF: () => dispatch(moveForwardsAction())
})

export default connect(null, mapDispathToProps)(QuestionnaireCardContact);