import React from 'react';
import CTA from '../../CTA/CTA.component';
import { connect } from 'react-redux';

import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';

import { moveForwardsAction } from '../../../redux/questionnaireStage/questionnaireStage.actions';

import './QuestionnaireCardNoHome.styles.scss';

const QuestionnaireCardNoHome = ({ moveF }) => {
    return (
        <section className='QuestionnaireCardNoHome' >
            <QuestionnaireTitle >Sorry, but to use solar you need to own a home</QuestionnaireTitle>
            <CTA onClick={moveF} >Go Back</CTA>
        </section>
    );
};

const mapDispatchToProps= dispatch => ({
    moveF: () => dispatch(moveForwardsAction())
})

export default connect(null, mapDispatchToProps)(QuestionnaireCardNoHome);