import React from 'react';
import './GlyphAndText.styles.scss';

const GlyphAndText = ({ src, text, alt, onClick }) => {
    return (
        <section className='GlyphAndText' onClick={onClick} >
            <img src={src} alt={`${alt} option`} className='GlyphAndText__Img' />
            <p className='GlyphAndText__Text' >{text}</p>
        </section>
    );
};

export default GlyphAndText;