import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect'

import InputBox from '../../InputBox/InputBox.component';
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import CTA from '../../CTA/CTA.component';

import './QuestionnaireCardName.styles.scss';

import { firstName, lastName } from '../../../redux/quoteForm/quoteForm.actions';
import { moveForwardsAction } from '../../../redux/questionnaireStage/questionnaireStage.actions';

const QuestionnaireCardName = ({ setFirstName, setLastName, moveF }) => {
    return (
        <section className='QuestionnaireCardName' >
            <QuestionnaireTitle >Who is this quote for?</QuestionnaireTitle>
            <InputBox placeholder='First Name' name='firstName' onChange={e => setFirstName(e.target.value)} />
            <InputBox placeholder='Last Name' name='lastName' onChange={e => setLastName(e.target.value)} />
            <CTA onClick={moveF} >Next</CTA> 
        </section>
    );
}
const mapDispatchToProps = dispatch => ({
    moveF: () => dispatch(moveForwardsAction()),
    setFirstName: name => dispatch(firstName(name)) ,
    setLastName: name =>dispatch(lastName(name))
});

export default connect(null, mapDispatchToProps)(QuestionnaireCardName);