import React from 'react';
import { connect } from 'react-redux';

import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import GlyphAndText from '../../GlyphAndText/GlyphAndText.component';

import check from '../../../assets/imgs/check.png';
import x from '../../../assets/imgs/x.png';

import { yes } from '../../../redux/quoteForm/quoteForm.actions';
import { moveForwardsAction, moveBackwardsAction } from '../../../redux/questionnaireStage/questionnaireStage.actions';

import './QuestionnaireCardHome.styles.scss';
 
const QuestionnaireCardHome = ({ ownsHome, moveF, moveB }) => {
    const handler = () => {
        ownsHome()
        return moveF()
    }
    return (
        <section className='QuestionnaireCardHome' >
            <QuestionnaireTitle >Do you own your home?</QuestionnaireTitle>
            <section className='QuestionnaireCardHome__Options' >
                <GlyphAndText src={check} text='yes' alt='Yes' onClick={handler} />
                <GlyphAndText src={x} text='no' alt='No' onClick={moveB} />
            </section>
        </section>
    );
};

const mapDispatchToProps = (dispatch) => ({
    ownsHome: () => dispatch(yes()),
    moveF: () => dispatch(moveForwardsAction()),
    moveB: () => dispatch(moveBackwardsAction())
})
 
 export default connect(null, mapDispatchToProps)(QuestionnaireCardHome);