import React from 'react';
import { connect } from 'react-redux';

import './QuestionnaireCardZipCode.styles.scss'
import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';
import InputBox from '../../InputBox/InputBox.component'
import CTA from '../../CTA/CTA.component';

import { moveForwardsAction } from '../../../redux/questionnaireStage/questionnaireStage.actions';

import { zipCode } from '../../../redux/quoteForm/quoteForm.actions';

const QuestionnaireCardZipCode = ({ zipCode, moveF }) => {
    return (
        <section className='QuestionnaireCardZipCode' >
            <QuestionnaireTitle >What's your Zip Code?</QuestionnaireTitle>
            <InputBox maxlenght={5} required minlength={5} onChange={(e) => zipCode(e.target.value)} name='zipCode' placeholder='Zip Code' styles='ZipCode' ></InputBox>
            <CTA styles='ZipCodeCTA' onClick={moveF} >Next</CTA>
        </section>
    );
}

const mapDispatchToProps = dispatch => ({
    zipCode: (billAmount) => dispatch(zipCode(billAmount)),
    moveF: () => dispatch(moveForwardsAction())
})

export default connect(null, mapDispatchToProps)(QuestionnaireCardZipCode);