import { createSelector } from 'reselect';

const selectQuoteForm = state => state.quoteForm;

export const selectoQuoteFormOwnership = createSelector(
    [selectQuoteForm],
    quoteForm => quoteForm.ownsHome
);

export const selectoQuoteFormZipCode = createSelector(
    [selectQuoteForm],
    quoteForm => quoteForm.zipCode
);

export const selectoQuoteFormBill = createSelector(
    [selectQuoteForm],
    quoteForm => quoteForm.eBill
);

export const selectoQuoteFormFirstName = createSelector(
    [selectQuoteForm],
    quoteForm => quoteForm.firstName
);

export const selectoQuoteFormLasttName = createSelector(
    [selectQuoteForm],
    quoteForm => quoteForm.lastName
);

export const selectoQuoteFormNumber = createSelector(
    [selectQuoteForm],
    quoteForm => quoteForm.number
);

export const selectoQuoteFormEmail = createSelector(
    [selectQuoteForm],
    quoteForm => quoteForm.email
);