import quoteFormTypes from './quoteForm.types';
const { ZIPCODE, EBILL, EMAIL, NUMBER, FIRSTNAME, LASTNAME, NO, YES } = quoteFormTypes

export const zipCode = (payload) => ({
    type: ZIPCODE,
    payload: payload
});

export const eBill = (payload) => ({
    type: EBILL,
    payload: payload
});

export const number = (payload) => ({
    type: NUMBER,
    payload: payload
});

export const email = (payload) => ({
    type: EMAIL,
    payload: payload
});

export const firstName = (payload) => ({
    type: FIRSTNAME,
    payload: payload
});

export const lastName = (payload) => ({
    type: LASTNAME,
    payload: payload
});

export const no = () => ({
    type: NO
})

export const yes = () => ({
    type: YES
})