import React from 'react';
import CTA from '../../CTA/CTA.component';
import InputBox from '../../InputBox/InputBox.component';
import { connect } from 'react-redux';

import './Booker.styles.scss';

import { modalToggleBooker } from '../../../redux/modal/modal.actions';

const Booker = ({ toggleModal}) => {
    return (
        <section className='Booker' onClick={toggleModal} >
            <p className='Booker__X' >X</p>
                <form id='booker' action="https://gmail.us8.list-manage.com/subscribe/post" method="POST" className='Booker__Form' onClick={e => e.stopPropagation()} >
                <input type="hidden" name="u" value="74e6de5699f8e9e2b792b749c" />
                <input type="hidden" name="id" value="9225535055" />
                <section className='Booker__Form__Container' >
                    <p className='Booker__Form__Container__Label' >First Name: </p>
                    <InputBox placeholder='First Name' name='MERGE1' /><br />
                    <p className='Booker__Form__Container__Label' >Last Nanme: </p>
                    <InputBox placeholder='Last Name' name='MERGE2' /><br />
                    <p className='Booker__Form__Container__Label' >Email: </p>
                    <InputBox placeholder='Email' name='MERGE0' type='email' /><br />
                    <p className='Booker__Form__Container__Label' >Phone Number: </p>
                    <InputBox placeholder='Phone #' name='MERGE4' type='number' /><br />
                    <p className='Booker__Form__Container__Label' >Pick a date for your appointment: </p>
                    <input className='Booker__Form__Container__DatePicker' name='MERGE3' id='booker' type='datetime-local' />
                    <CTA form='booker' type='submit' onClick={null} >Submit</CTA>
                </section>
               
            </form>
        </section>
    );
};

const mapDispathToProps= dispatch => ({
    toggleModal: () => dispatch(modalToggleBooker())
});

export default connect(null, mapDispathToProps)(Booker);