import quoteFormTypes from './quoteForm.types';

const { ZIPCODE, EBILL, NUMBER, EMAIL, FIRSTNAME, LASTNAME, NO, YES } = quoteFormTypes;

const INITIAL_STATE = {
    ownsHome: undefined,
    zipCode: '',
    eBill: 0,
    number: '',
    email: '',
    firstName: '',
    lastName: ''
}

const quoteFormReducer = (state = INITIAL_STATE, action) => {
    switch(action.type) {
        case ZIPCODE:
            return {
                ...state,
                zipCode: action.payload
            }
        
        case EBILL:
            return {
                ...state,
                eBill: action.payload
            }
        
        case NUMBER:
            return {
                ...state,
                number: action.payload
            }

            case EMAIL:
                return {
                    ...state,
                    email: action.payload
                }
        
        case FIRSTNAME:
            return {
                ...state,
                firstName: action.payload
            }

            case LASTNAME:
            return {
                ...state,
                lastName: action.payload
            }
            
        case NO:
            return {
                ...state,
                ownsHome: false
            }
        case YES:
            return {
                ...state,
                ownsHome: true
            }
        default:
            return state;
    }
}

export default quoteFormReducer;