import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';

import './Questionnaire.styles.scss'

import { selectQuestionnairePosition } from '../../redux/questionnaireStage/questionnaireStage.selector';

import { moveForwardsAction, moveBackwardsAction } from '../../redux/questionnaireStage/questionnaireStage.actions';

import Ticket from '../Ticket/Ticket.component';

import { selectTicketStatus } from '../../redux/ticket/ticket.selector';
import { selectwindowWidthValue } from '../../redux/windowWidth/windowWidth.selector';

import QuestionnaireCardNoHome from '../QuestionnaireCards/QuestionnaireCardNoHome/QuestionnaireCardNoHome.component';
import QuestionnaireCardZipCodeComponent from '../QuestionnaireCards/QuestionnaireCardZipCode/QuestionnaireCardZipCode.component';
import QuestionnaireCardHome from '../QuestionnaireCards/QuestionnaireCardHome/QuestionnaireCardHome.component';
import QuestionnaireCardBill from '../QuestionnaireCards/QuestionnaireCardBill/QuestionnaireCardBill.component';
import QuestionnaireCardContact from '../QuestionnaireCards/QuestionnaireCardContact/QuestionnaireCardContact.component';
import QuestionnaireCardName from '../QuestionnaireCards/QuestionnaireCardName/QuestionnaireCardName.component';
import QuestionnaireCardFinish from '../QuestionnaireCards/QuestionnaireCardFinish/QuestionnaireCardFinish.component';

const Questionnaire = ({ stagePosition, mobileOnly, ticketStatus, windowWidth }) => {
    const cards = [ QuestionnaireCardNoHome, QuestionnaireCardHome, QuestionnaireCardZipCodeComponent, QuestionnaireCardBill, QuestionnaireCardName, QuestionnaireCardContact, QuestionnaireCardFinish ]
    let QuestionnaireQuestion = cards[stagePosition]
    return (
        <section className={`Questionnaire ${mobileOnly ? 'mobileOnly' : ''}`} >
            {
                ticketStatus && windowWidth < 768 ? <Ticket >Exit</Ticket> : null
            }
            <section className='Questionnaire__Container' >
                <QuestionnaireQuestion />
            </section>
            
        </section>
    );
}

const mapStateToProps = createStructuredSelector({
    stagePosition: selectQuestionnairePosition,
    ticketStatus: selectTicketStatus,
    windowWidth: selectwindowWidthValue
});

const mapDispathToProps = dispatch => ({
    moveBackwardsAction: () => dispatch(moveBackwardsAction()),
    moveForwardsAction: () => dispatch(moveForwardsAction())
})

export default connect(mapStateToProps, mapDispathToProps)(Questionnaire);