import React from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';

import QuestionnaireTitle from '../../QuestionnaireTitle/QuestionnaireTitle.component';

import './QuestionnaireCardBill.styles.scss';

import CTA from '../../CTA/CTA.component';

import { selectoQuoteFormBill } from '../../../redux/quoteForm/quoteForm.selector';

import { moveForwardsAction } from '../../../redux/questionnaireStage/questionnaireStage.actions';
import { eBill } from '../../../redux/quoteForm/quoteForm.actions';

const QuestionnaireCardBill = ({ billCounter, billCounterAction, moveF }) => {
    return (
        <section className='QuestionnaireCardBill' >
            <QuestionnaireTitle >What is your average Electrical Bill?</QuestionnaireTitle>
            <p className='QuestionnaireCardBill__Counter' >
                {`US$${billCounter}`}
            </p>
            <input onChange={(e) => billCounterAction(e.target.value)} id='eBillRange' className='QuestionnaireCardBill__Input' defaultValue={100} type='range' min='0' max='1500' list='range' />
            <datalist id='range' >
                <option value='0' label='$0' ></option>
                <option value='1500' label='$1500' ></option>
            </datalist>
            <CTA onClick={moveF} >Next</CTA>
        </section>
    );
}

const mapStateToProps = createStructuredSelector({
    billCounter: selectoQuoteFormBill
})

const mapDispatchToProps = dispatch => ({
    moveF: () => dispatch(moveForwardsAction()),
    billCounterAction: quantity => dispatch(eBill(quantity))
})

export default connect(mapStateToProps, mapDispatchToProps)(QuestionnaireCardBill);